<template>
  <v-container fluid pt-0>
    <v-layout justify-center>
      <v-flex xs12 sm12 md9 lg6>
          <v-layout column mb-3 bw>
            <antibiogram-summary :routeUrl="`/pathogens/${$route.params.pathogenId}/antibiogram`"></antibiogram-summary>
          </v-layout>
          <v-layout column>
            <v-flex font-weight-bold mb-2>
                Susceptibility of <span class="primary--text subheading font-weight-light font-italic">{{pathogenName}}</span>
            </v-flex>
            <v-layout column>
              <v-tabs
                  dark
                  grow
                  slider-color="primary"
                  @change="changeComp()"
              >
                  <v-tab
                  v-for="tab in tabs"
                  :key="tab"
                  ripple
                  >{{tab}}</v-tab>
                  <v-tab-item
                  v-for="n in 3"
                  :key="n"
                  lazy
                  ></v-tab-item>
              </v-tabs>
              <component transition="slide-y-transition" :is="comp" :susceptibilities="antibiogram.susceptibilities"></component> 
            </v-layout>
          </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import AntibiogramTable from "@/components/scoped/antibiogram-table";

export default {
    components: { 
        'antibiogram-table': () =>
            import(/* webpackChunkName: "antibiogram-table" */ "@/components/scoped/antibiogram-table"),
        'antibiogram-chart': () =>
            import(/* webpackChunkName: "antibiogram-chart" */ "@/components/scoped/antibiogram-chart"),
        'antibiogram-trend': () =>
            import(/* webpackChunkName: "antibiogram-trend" */ "@/components/scoped/antibiogram-trend"),
    },
    computed: {
        ...mapGetters(['antibiograms', 'allPathogens']),
        comp() {
            return this.component
        },
        pathogenName() {
            return this.allPathogens.find(pathogen => pathogen.pathogenId === parseInt(this.$route.params.pathogenId)).pathogenName
        }
    },
    created(){
        this.antibiogram = this.antibiograms.find(antibiogram => antibiogram.pathogen === 'Pseudomonas aeruginosa')
        console.log(this.antibiograms)
    },
    data() {
        return {
            tabs: ["Table", "Chart","Trend"],
            component: "antibiogram-table",
            antibiogram: {}
        }
    },
    methods: {
        goto (id) { this.$router.push(`/antimicrobials/${id}`) },
        viewAntibiogram(id) { this.$router.push(`/antibiogram`) },
        viewAntibiogram(facility) {
            this.$store.commit('updateAntibiogramFilter', {
                scope: "Facility/Hospital Level",
                duration: "1 year",
                values: [facility],
                advanced: false,
            })
            this.$router.push('/antibiogram')
        },
        changeComp(n) {
        switch (n) {
            case 0:
            this.component = "antibiogram-table";
            break;
            case 1:
            this.component = "antibiogram-chart";
            break;
            case 2:
            this.component = "antibiogram-trend";
            break;
        }
        }
  }
};
</script>

